.fundraisers {
    h2 {
        margin-bottom: 0;
        &+hr {
            margin-top: 2px;
        }
    }
    &__content {
        padding: 15px 30px 30px;

        border-radius: 6px;
        background-color: $color__ghostwhite;
        box-shadow: 0 4px 6px rgba(50,50,93,.11), 0 1px 3px rgba(0,0,0,.08);
    }
    &__list {
        list-style: none;
        line-height: 1.7;

        counter-reset: heading;
        .fundraisers__item:before {
            content: counter(heading) ' - ';
            counter-increment: heading;
        }
        &_blue {
        	a {
        		color: $color__blue;
        		&:hover,
        		&:focus {
        			color: $color__blue_darknet;
        		}
        	}
        }
    }
        padding-top: 0;
}