.whowalk {

    overflow: hidden;
    padding: 0;

    color: $color__white;
        position: relative;
        padding-top: 92px;
        @media screen and (max-width: 767px) {
            padding-top: 71px;
        }
    & .container {
        padding: 50px 15px;
        & h1 {
            margin: 0 0 .4em;

            font-size: 32px;
            line-height: 1.3;
            @media screen and (min-width: 768px) {
                max-width: 560px;

                font-size: 50px;
            }
            @media screen and (min-width: 992px) {
                max-width: 730px;

                font-size: 66px;
            }
        }
        & p {
            max-width: 640px;
            margin-bottom: 1em;

            font-size: 28px;
            font-weight: 200;
            line-height: 1.3;
        }
        @media screen and (min-width: 768px) {
            padding: 60px 15px;
        }
        @media screen and (min-width: 992px) {
            padding: 70px 15px;
        }
        @media screen and (min-width: 1200px) {
            width: inherit;
            max-width: 1430px;
            padding: 80px;
        }
    }
}